import React, { FC, useState } from 'react';
import Helmet from 'react-helmet';
import Splash from 'components/Splash';
import About from 'components/About';
import { Nav } from 'components/navigation';
import styles from 'scss/index.module.scss';
import { BaseTemplate } from 'templates';

const Index: FC = () => {
  const [isActive, setIsActive] = useState(false);

  const showNavbar = (shouldShowNavBar) => {
    setIsActive(shouldShowNavBar);
  };

  const [shouldShowEmailCta, setShouldShowEmailCta] = useState(false);

  return (
    <BaseTemplate>
      <Helmet
        title={'Adam J Peña - Innovating Front-End Development & UX/UI'}
      />
      <div className={styles.index}>
        <Splash>
          <Nav
            isSticky={isActive}
            isDark={!isActive}
            revealAfterSplash
            {...{ setShouldShowEmailCta, shouldShowEmailCta, showNavbar }}
          />
        </Splash>
        <About />
      </div>
    </BaseTemplate>
  );
};

export default Index;
